import ProductsSold from "./ProductsSold";
import { UserContext } from "../../components/UserContext";
import { useContext, useEffect, useState } from "react";
import { Spinner } from "../spinner/Spinner";
import { Error } from "../error/Error";
import RoyalDaily from "./RoyalDaily";
import { BASE_URL, URLS } from "../../pages/url";
import { ProductSummary, DailySummary } from "./Summary";
import { Date } from "../date/Date";

function getURL(sheet_name, from, to) {
  const URL =
    BASE_URL +
    URLS.report +
    "?" +
    "sheet_name=" +
    sheet_name +
    "&" +
    "from=" +
    from +
    "&" +
    "to=" +
    to;
  return URL;
}

export function DailySheet({ from, to }) {
  const { loading, setLoading } = useContext(UserContext);
  const [error, setError] = useState(null);
  const [data, setData] = useState(null);
  const URL = getURL("Royal Daily", from, to);

  useEffect(() => {
    fetch(URL)
      .then((response) => {
        if (response.ok) return response.json();
        throw response;
      })
      .then((data) => {
        setData({
          sheet: JSON.parse(data.todayhisab.sheet),
          summary: data.todayhisab.summary,
        });
      })
      .catch((error) => {
        console.error("Error Fetching data: ", error);
        setError(error);
      })
      .finally(() => {
        setLoading(false);
      });
  }, [from, to]);

  return (
    <div>
      {loading ? (
        <Spinner />
      ) : (
        <div>
          {error ? (
            <Error />
          ) : (
            <div>
              <Date from={from} to={to} />
              <DailySummary data={data.summary} />
              {data.sheet.length !== 0 ? (
                <RoyalDaily sheet={data.sheet} summary={data.summary} />
              ) : (
                <div> Nothing to see</div>
              )}
            </div>
          )}
        </div>
      )}
    </div>
  );
}

