import { useState, useEffect } from "react";
import { Spinner } from "../../components/spinner/Spinner";
import { Error } from "../../components/error/Error";
import { BASE_URL, URLS } from "../url";

import {
  DailySummary,
  ProductSummary,
  MoneySpent,
} from "../../components/sheets/Summary";

const Home = () => {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);
  const [data, setData] = useState(null);
  const URL = BASE_URL + URLS.home;

  useEffect(() => {
    fetch(URL)
      .then((response) => {
        if (response.ok) return response.json();
        throw response;
      })
      .then((data) => {
        setData(data.summary);
      })
      .catch((error) => {
        console.error("Error Fetching data: ", error);
        setError(error);
      })
      .finally(() => {
        setLoading(false);
      });
  }, []);
  console.log(data);
  return (
    <div>
      {loading ? (
        <Spinner />
      ) : (
        <div>
          {error ? (
            <Error />
          ) : (
            <div>
              <DailySummary data={data["Royal Daily"]} />
              <ProductSummary data={data["Products Sold"]} />
              <MoneySpent data={data["money_spent"]} />
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default Home;
