export const Date = ({ from, to }) => {
  return (
    <div className="flex justify-center items-center text-2xl md:text-4xl text-gray-700 ">
      {from === to ? (
        <div> {from}</div>
      ) : (
        <div>
          {" "}
          {from} to {to}{" "}
        </div>
      )}
    </div>
  );
};
