import { useContext, useState } from "react";
import { ProductSheet } from "../../components/sheets/Sheets";
import { UserContext } from "../../components/UserContext";
import { Calender } from "../../components/calender/Calender";

const Report = () => {
  const { date } = useContext(UserContext);
  return date["from"] !== null && date["to"] !== null ? (
    <ProductSheet from={date["from"]} to={date["to"]} />
  ) : (
    <div> Nothing </div>
  );
};

const ProfitReport = () => {
  const [date, setDate] = useState({
    from: null,
    to: null,
  });
  const [loading, setLoading] = useState(false);

  return (
    <div>
      <UserContext.Provider value={{ date, setDate, loading, setLoading }}>
        <Calender />
        <Report />
      </UserContext.Provider>
    </div>
  );
};

export default ProfitReport;
